// Footer020
footer {
	.footertop {
		border-top: 2px solid $brown;
		padding: 30px 0;
		.footernav {
			margin-bottom: 10px;
			ul {
				li {
					display: inline-block;
					@include fs(12rem);
					font-weight: 700;
					text-transform: uppercase;
					+ li {
						&:before {
							content: '|';
							margin-left: 7px;
							margin-right: 10px; } }
					a {
						color: $main;
						&:hover {
							color: $brown; } } } } }
		.social {
			margin-bottom: 10px;
			a {
				display: inline-block;
				color: $brown;
				width: 30px;
				height: 30px;
				@include fs(18rem);
				@include bor-rad(50%);
				text-align: center;
				line-height: 30px;
				@include pos(rel,null);
				.fa {
					@include pos(rel,null);
					z-index: 5; }
				&:before {
					content: '';
					display: block;
					@include pos(abs, 0 null 0 0);
					right: 0;
					@include bor-rad(50%);
					background: $brown;
					@include transform(scale3d(0,0,0));
					@include transition(all,0.5s); }
				&:hover {
					color: $white;
					&:before {
						@include transform(scale3d(1,1,1)); } } } }
		.contactinfo {
			.company {
				font-weight: 700;
				text-transform: uppercase;
				color: $brown;
				margin-bottom: 15px; }
			ul {
				li {
					color: darken($white,60%);
					.fa {
						color: $main; } } } } }
	.footerbottom {
		background: $brown;
		.copyright {
			@include fs(13rem);
			color: $white;
			line-height: 30px; } } }

.up {
	@include pos(fix, null 10px 10px null);
	width: 40px;
	height: 40px;
	@include bor-rad(50%);
	background: darken($brown,10%);
	color: $white;
	@include fs(18rem);
	line-height: 40px;
	text-align: center;
	cursor: pointer;
	display: none; }
