.partner-page {}

.partner-group {
	margin-bottom: 30px;
	.posttitle {
		margin-bottom: 25px; }
	.col-lg-3 {
		@media (min-width: 1600px) {
			width: 20%; } }
	.partner-col {
		margin-bottom: 30px;
		figure {
			height: 120px;
			border: 1px solid darken($white,10%);
			display: flex;
			width: 100%;
			justify-content: center;
			align-items: center;
			overflow: hidden;
			cursor: pointer;
			img {
				max-width: 70%;
				max-height: 70%;
				@include transition(all,0.3s); }
			&:hover {
				img {
					@include transform(scale3d(1.2,1.2,1)); } } } } }
