.about-page {
	padding: 60px 0;
	.about-pic {
		overflow: hidden;
		@include pos(rel,null);
		@include media-breakpoint-down(md) {
			margin-bottom: 30px; }
		&:before {
			content: '';
			display: block;
			@include pos(abs, 30px calc(100% - 20px) 30px 20px);
			border-top: 1px solid $white;
			border-bottom: 1px solid $white;
			opacity: 0.5;
			z-index: 5;
			@include transition(all,0.5s); }
		&:after {
			content: '';
			display: block;
			@include pos(abs, 20px 30px calc(100% - 20px) 30px);
			border-right: 1px solid $white;
			border-left: 1px solid $white;
			opacity: 0.5;
			z-index: 5;
			@include transition(all,0.5s); }
		.img {
			height: 450px;
			@include pos(rel,null);
			@include transition(all,10s); }
		&:hover {
			&:before {
				right: 20px; }
			&:after {
				bottom: 20px; }
			.img {
				@include transform(scale3d(1.2,1.2,1)); } } }
	.about-content {
		padding: 40px 0;
		height: 100%;
		@include flexbox;
		flex-wrap: wrap;
		justify-content: center;
		align-content: center;
		align-items: center;
		text-align: center;
		.pagetitle {
			width: 100%; }
		.content {
			width: 100%; } } }
.about-gallery {
	margin-bottom: 60px;
	.albumlist {
		.album {
			width: 25%;
			overflow: hidden;
			@include media-breakpoint-down(md) {
				width: 50%; }
			.pic {
				display: block;
				height: 250px;
				@include pos(rel,null);
				@include transition(all,10s);
				@include media-breakpoint-down(md) {
					height: 180px; }
				&:before {
					content: '\f0c1';
					font-family: 'fontawesome';
					@include pos(abs, 50% null null 50%);
					@include transform(translate(-50%, -50%));
					width: 40px;
					height: 40px;
					line-height: 40px;
					text-align: center;
					background: color-rgba($white,0.8);
					color: $brown;
					@include fs(18rem);
					@include bor-rad(50%);
					z-index: 10;
					opacity: 0;
					@include transition(all,0.5s); }
				&:after {
					content: '';
					@include pos(abs, 0 null 0 0);
					right: 0;
					background: color-rgba($brown,0.5);
					z-index: 5;
					opacity: 0;
					@include transition(all,0.5s); } }
			&:hover {
				.pic {
					@include transform(scale3d(1.2,1.2,1));
					&:before, &:after {
						opacity: 1; } } } } } }

.chef-slide {
	// width: 60%
	margin: 40px auto;
	.slick-prev, .slick-next {
		@include pos(abs, 50% null null null);
		@include transform(translateY(-50%));
		@include fs(0);
		z-index: 5;
		&:before {
			content: '';
			font-family: 'fontawesome';
			@include fs(32rem); } }
	.slick-prev {
		left: 15px;
		&:before {
			content: '\f104'; } }
	.slick-next {
		right: 15px;
		&:before {
			content: '\f105'; } }
	.item {
		.chef-img {
			display: block;
			height: 500px;
			background: darken($white,5%);
			text-align: center;
			overflow: hidden;
			img {
				display: initial;
				height: 100%;
				width: unset;
				max-width: unset; } } } }
