.breadcrumb-wrap {
	@include pos(rel,null);
	background: darken($white,5%);
	// margin-bottom: 40px
	@include pos(rel,null);
	.breadcrumb-bg {
		@include pos(abs, 0 null 0 0);
		right: 0;
		filter: brightness(70%); }
	.pagename {
		display: inline-block;
		color: $white;
		@include fs(32rem);
		@include pos(rel,null);
		font-family: 'dancing script';
		@include fs(42rem);
		text-transform: capitalize;
		margin: 250px 0 20px;
		&:after {
			content: '';
			display: block;
			width: 60px;
			height: 1px;
			background: $white;
			margin-top: 10px; } }
	.breadcrumb {
		margin: 0 0 30px;
		padding: 0;
		background: none;
		@include bor-rad(0);
		@include pos(rel,null);
		li {
			display: inline-block;
			color: $white;
			@include media-breakpoint-down(md) {
				@include fs(13rem); }
			&:after {
				content: '\f105';
				font-family: 'fontawesome';
				margin: 0 3px 0 5px; }
			&:last-child {
				&:after {
					display: none; } }
			a {
				color: $white;
				span {
 }					// font-weight: 300
				&:hover {
					color: $white;
					text-decoration: underline; } } } } }
.productpage {
	.breadcrumb-wrap {
		@include media-breakpoint-down(md) {
			margin-top: 36px; } } }
.productdetailpage {
	.breadcrumb-wrap {
		.breadcrumb-bg {
			display: none; } } }
