.service-page {
	padding: 60px 0;
	.service-list {
		.service-col {
			width: 33.33%;
			@include media-breakpoint-down(md) {
				width: 50%; }
			@include media-breakpoint-down(sm) {
				width: 100%; }
			figure {
				@include pos(rel,null);
				overflow: hidden;
				.service-img {
					display: block;
					img {
						width: 100%;
						filter: grayscale(100%);
						@include transition(all,0.5s); } }
				.service-name {
					display: block;
					background: color-rgba($brown,0.8);
					padding: 7px 10px;
					line-height: 1.5;
					text-transform: uppercase;
					font-weight: 600;
					@include fs(18rem);
					color: $white;
					@include transition(all,0.5s);
					@include pos(abs, 50px null null 0);
					@include media-breakpoint-down(lg) {
						@include fs(15rem); } }
				figcaption {
					@include pos(abs, null 0 0 0);
					@include transform(translateY(100%));
					@include transition(all,0.5s);
					z-index: 5;
					.service-title {
						background: color-rgba($brown,0.8);
						padding: 7px 10px;
						line-height: 1.5;
						text-transform: uppercase;
						font-weight: 600;
						@include fs(18rem);
						color: $white;
						@include transition(all,0.5s);
						display: inline-block; }
					.service-content {
						@include fs(14rem);
						// border-top: 2px solid color-rgba($brown,0.8)
						background: color-rgba($black,0.8);
						padding: 20px;
						color: color-rgba($white,0.8); } }
				&:hover {
					.service-img {
						img {
							filter: grayscale(0); } }
					.service-name {
						@include transform(translateX(-100%)); }
					figcaption {
						@include transform(translateY(0%)); } } } } } }

.othertitle {
	@include fs(24rem);
	font-weight: 600;
	margin-bottom: 20px;
	&:after {
		content: '';
		display: block;
		width: 60px;
		height: 1px;
		background: $brown;
		margin-top: 10px; } }

.other-service {
	margin-bottom: 60px;
	.other-col {
		@include media-breakpoint-down(sm) {
			margin-bottom: 30px; }
		figure {
			@include pos(rel,null);
			.other-img {
				display: block;
				@include bor-rad(5px);
				overflow: hidden;
				img {
					@include transition(all,10s); } }
			figcaption {
				.other-name {
					@include pos(abs, null null 10px 0);
					a {
						color: $white;
						display: block;
						background: color-rgba($brown,0.8);
						text-transform: uppercase;
						font-weight: 600;
						padding: 7px 10px; } } }
			&:hover {
				.other-img {
					img {
						@include transform(scale3d(1.2,1.2,1)); } }
				figcaption {
					.other-name {
						a {
							background: color-rgba($main,0.8); } } } } } } }

.pagedes {
	text-align: center;
	max-width: 80%;
	margin: -10px auto 30px;
	@include media-breakpoint-down(md) {
		max-width: 100%; }
	@include media-breakpoint-down(sm) {
		max-width: 100%; } }
.food-page {
	padding: 60px 0;
	article {
		margin-bottom: 30px; }
	.food-col {
		height: 100%;
		background: $white;
		box-shadow: 0px 3px 20px color-rgba($black,0.1);
		@include bor-rad(5px);
		padding: 20px;
		@include transition(all,0.5s);
		cursor: pointer;
		.title {
			@include fs(16rem);
			font-weight: 700;
			text-transform: uppercase;
			margin-bottom: 15px;
			// +transition(all,0.5s)
			display: block;
			color: $black;
			&:after {
				content: '';
				display: block;
				width: 30px;
				height: 2px;
				background: $brown;
				margin-top: 10px;
				@include transition(all,0.5s); }
			.des {
 }				// +transition(all,0.5s)
			.imglist {
				display: none; } }
		&:hover {
			background: $brown;
			color: $white;
			box-shadow: 0px 3px 30px color-rgba($black,0.1);
			.title {
				color: $white;
				&:after {
					background: $white;
					opacity: 0.5; } } } } }
.bookingfrm {
	margin-bottom: 40px;
	box-shadow: 0px 3px 30px color-rgba($black,0.1);
	@include bor-rad(5px);
	border: 1px solid darken($white,10%);
	padding: 20px;
	.form-group {
		margin-bottom: 10px;
		label {
			display: block; }
		input, select, textarea {
			width: 100%;
			border: 1px solid darken($white,10%);
			@include bor-rad(3px);
			padding: 7px 10px; }
		select {
			padding: 6px 10px; } } }
.btn-continue {
	display: inline-block;
	line-height: 36px;
	padding: 0px 20px;
	@include bor-rad(3px);
	background: $brown;
	color: $white !important;
	@include fs(14rem);
	font-weight: 600;
	text-transform: uppercase;
	&:hover {
		color: $white !important;
		background: lighten($brown,10%); } }
.banklist {
	.bank {
		width: 33.33%;
		padding: 0px 5px;
		margin-bottom: 10px;
		.item {
			height: 80px;
			@include bor-rad(5px);
			border: 1px solid darken($white,10%);
			@include flexbox;
			justify-content: center;
			align-items: center;
			cursor: pointer;
			img {
				max-width: 80%;
				max-height: 80%; } }
		&.active {
			.item {
				border-color: $brown; } } } }

.alert {
	text-align: center; }

.speciallist {
	article {
		margin-bottom: 30px; }
	.food {
		height: 100%;
		@include bor-rad(5px);
		overflow: hidden;
		border: 1px solid darken($white,10%);
		@include transition(all,0.5s);
		&:hover {
			box-shadow: 0 5px 20px color-rgba($black,0.1); }
		figure {
			.foodimg {
				display: block;
				img {
					width: 100%; } }
			figcaption {
				padding: 15px;
				.checkbox {
					margin-bottom: 0;
					min-height: 44px;
					label {
						font-weight: 700;
						text-transform: uppercase; } }
				.price {
					color: $brown;
					margin-left: 25px; } } } }
	.btn-book {
		@include mainbutton;
		background: $brown;
		text-transform: uppercase;
		&:hover {
			background: lighten($brown,10%); } } }
