
// HEADER 002

header {
	@include pos(fix, 0 0 null 0);
	z-index: 1000;
	@include transition(all,0.5s);
	.container {
		@include media-breakpoint-down(md) {
			max-width: 100%;
			width: 100%; } }
	.headertop {
		background: color-rgba($brown,0.8);
		line-height: 30px; }
	.topinfo {
		float: left;
		@include media-breakpoint-down(xs) {
			display: none; }
		.hotline, .email, time {
			display: block;
			float: left;
			color: $white;
			@include fs(13rem);
			margin-right: 20px;
			a {
				color: $white; }
			.fa {
				margin-right: 5px; } } }
	.social {
		float: right;
		a {
			display: block;
			float: left;
			width: 30px;
			line-height: 30px;
			@include bor-rad(50px);
			color: $white;
			text-align: center;
			background: color-rgba($black,0);
			&:hover {
				background: color-rgba($black,0.2); } } }
	.headermain {
		background: linear-gradient(color-rgba($black,0.5),color-rgba($black,0));
		@include transition(all,0.5s); }
	.sitelogo {
		float: left;
		padding: 15px 0;
		@include media-breakpoint-down(md) {
			padding: 15px 0; }
		a {
			display: block;
			img {
				height: 40px;
				@include transition(all,0.5s);
				@include media-breakpoint-down(md) {
					height: 30px; } } } }
	.overlay {
		@include pos(fix, 0 null 0 0);
		right: 0;
		background: $black;
		opacity: 0.7;
		display: none;
		z-index: 100; }
	.btn-showmenu {
		display: none;
		float: left;
		margin: 21px 0;
		width: 20px;
		height: 16px;
		margin-right: 15px;
		@include pos(rel,null);
		z-index: 110;
		span {
			display: block;
			width: 24px;
			height: 2px;
			width: 100%;
			background: $white;
			@include pos(abs, 50% null null null);
			@include transform(translateY(-50%));
			@include transition(all,0.5s); }
		&:before {
			content: "";
			display: block;
			height: 2px;
			background: $white;
			@include pos(abs, 0 0 null 0);
			@include transition(all,0.5s); }
		&:after {
			content: "";
			display: block;
			height: 2px;
			background: $white;
			@include pos(abs, null 0 0 0);
			@include transition(all,0.5s); }
		&.active {
			span {
				opacity: 0; }
			&:before {
				@include transform(translateY(-50%)rotateZ(45deg));
				top: 50%; }
			&:after {
				@include transform(translateY(50%)rotateZ(-45deg));
				bottom: 50%; } }
		@include media-breakpoint-down(md) {
			display: block; } }
	.menu {
		float: right;
		padding: 20px 0;
		@include media-breakpoint-down(md) {
			padding: 0;
			float: none;
			@include pos(fix, 90px 0 null 0);
			padding: 20px 0;
			background: color-rgba($brown,0.9);
			display: none; }
		> ul {
			float: left;
			@include media-breakpoint-down(md) {
				float: none;
				margin: 0 auto;
				max-width: 300px; }
			> li {
				float: left;
				@include pos(rel,null);
				@include transition(all,0.5s);
				@include media-breakpoint-down(md) {
					float: none;
					text-align: center; }
				+ li {
					@include media-breakpoint-up(xl) {
						margin-left: 10px; } }
				> a {
					display: block;
					color: $white;
					line-height: 30px;
					overflow: visible;
					font-weight: 700;
					text-transform: uppercase;
					@include fs(13rem);
					@include bor-rad(5px);
					padding: 0px 15px;
					background: transparent;
					box-sizing: border-box;
					@include media-breakpoint-down(md) {
						color: $white;
						line-height: 50px;
						@include fs(16rem);
						font-weight: 600; } }
				&:last-child {
					@include media-breakpoint-up(lg) {
						a {
							line-height: 28px;
							border: 1px solid color-rgba($white,0.5); } } }
				&:hover {
					@include media-breakpoint-up(lg) {
						a {
							background: color-rgba($white,0.2); } } }
				&.active {
					@include media-breakpoint-up(lg) {
						a {
							color: $white;
							background: color-rgba($brown,0.5); } }
					@include media-breakpoint-down(md) {
						a {
							color: $brown;
							background: color-rgba($white,0.8); } } } } } }
	.searchtoggle {
		color: $white;
		float: left;
		line-height: 30px;
		margin-left: 30px;
		cursor: pointer;
		@include fs(18rem);
		@include transition(all,0.5s);
		@include media-breakpoint-down(md) {
			float: right;
			margin: 15px 0; }
		&:hover {
			color: $brown; } }
	.search {
		@include pos(fix, 0 null 0 0);
		right: 0;
		z-index: 900;
		background: color-rgba($brown,0.9);
		@include flexbox;
		justify-content: center;
		align-items: center;
		visibility: hidden;
		opacity: 0;
		@include transform(scale3d(2,2,2));
		@include transition(all,0.5s);
		&.open {
			visibility: visible;
			opacity: 1;
			@include transform(scale3d(1,1,1)); }
		.btn-closesearch {
			@include fs(24rem);
			color: $white;
			@include pos(abs, 30px 30px null null);
			z-index: 10;
			cursor: pointer; }
		.searchwrap {
			@include flexbox;
			width: 450px;
			@include bor-rad(5px);
			border: 1px solid color-rgba($white,0.5);
			@include media-breakpoint-down(md) {
				width: 400px; }
			@include media-breakpoint-down(sm) {
				width: 300px; }
			@include media-breakpoint-down(xs) {
				width: 280px; }
			&:hover, &:active, &:focus {
				border-color: color-rgba($white,0.8); }
			input {
				line-height: 40px;
				border: none;
				background: none;
				color: $white;
				padding: 0px 20px;
				width: calc(100% - 40px); }
			.searchbutton {
				color: $white;
				width: 40px;
				line-height: 40px;
				text-align: center;
				@include fs(18rem); } } }
	&.minimal {
		// background: $white
		box-shadow: 0 3px 30px color-rgba($black,0.05);
		.sitelogo {
			a {
				img {
					filter: brightness(0); } } }
		.btn-showmenu {
			span, &:before, &:after {
				background: $main; } }
		.headermain {
			background: color-rgba($white,0.9); }
		.menu {
			.menulink {
				@include media-breakpoint-up(lg) {
					> li {
						a {
							color: $main; }
						&:last-child {
							a {
								border-color: color-rgba($black,0.2); } }
						&:hover {
							a {
								color: $brown; } } } } } }
		.searchtoggle {
			color: $main;
			&:hover {
				color: $brown; } } } }
