main {
 }	// color: $white
.headtitle {
	@include fs(28rem);
	font-weight: 700;
	text-align: center;
	text-transform: uppercase;
	margin-bottom: 40px;
	&:after {
		content: '';
		display: block;
		width: 60px;
		height: 2px;
		margin: 10px auto 0;
		background: $brown; } }
.home-about {
	padding: 60px 0;
	.about-content {
		@include media-breakpoint-down(lg) {
			padding-bottom: 40px; }
		@include media-breakpoint-down(md) {
			padding-bottom: 0; }
		figure {
			@include flexbox;
			flex-wrap: wrap;
			@include pos(rel,null);
			.img {
				width: 66.66%;
				@include media-breakpoint-down(md) {
					width: 100%; }
				img {
					width: 100%; } }
			figcaption {
				@include pos(abs, 50px 0 null null);
				width: 50%;
				padding: 30px;
				background: $white;
				box-shadow: 0px 10px 30px color-rgba($black,0.1);
				text-align: center;
				@include media-breakpoint-down(md) {
					@include pos(static,unset);
					width: 100%; }
				@include media-breakpoint-down(sm) {
					padding: 20px; }
				@include media-breakpoint-down(xs) {
					padding: 15px; }
				.content {
					margin-bottom: 10px; }
				.link {
					display: inline-block;
					line-height: 36px;
					@include fs(13rem);
					padding: 0px 20px;
					color: $white;
					text-transform: uppercase;
					background: $brown;
					&:after {
						content: '\f105';
						font-family: 'fontawesome';
						margin-left: 5px; }
					&:before {
						background: color-rgba($white,0.2); } } } } } }
.pagetitle {
	font-family: 'dancing script';
	font-weight: 700;
	@include fs(42rem);
	color: $brown;
	margin-bottom: 30px;
	text-align: center;
	text-transform: capitalize;
	@include media-breakpoint-down(md) {
		@include fs(36rem); }
	@include media-breakpoint-down(sm) {
		@include fs(28rem); }
	&:after {
		content: '';
		display: block;
		width: 60px;
		height: 1px;
		background: $black;
		margin: 10px auto 0; } }
