.career-page, .col-main {
	margin-bottom: 40px;
	.menu-job {
		margin: 0px 0 10px;
		padding: 0;
		border: none;
		z-index: 5;
		@include media-breakpoint-down(sm) {
			// white-space: nowrap
 }			// overflow: auto
		a {
			display: inline-block;
			@include fs(13rem);
			text-transform: uppercase;
			@include bor-rad(50px);
			border: 1px solid darken($white,40%);
			line-height: 36px;
			margin: 0;
			margin-bottom: 10px;
			padding: 0 15px;
			color: $lblack;
			@include media-breakpoint-down(sm) {
				@include fs(12rem);
				line-height: 32px;
				padding: 0px 10px;
				width: 100%;
				text-align: center; }
			@include media-breakpoint-down(xs) {
				@include fs(11rem);
				line-height: 30px;
				padding: 0 5px; }
			+ a {
				margin-left: 5px;
				@include media-breakpoint-down(sm) {
					margin-left: 0; } }
			&.active, &:hover {
				border-color: lighten($red,10%);
				font-weight: 700;
				color: lighten($red,10%); } } }
	.job-wrap {
		.table {
			border: none;
			margin: 0;
			padding: 0;
			// min-width: 800px
			th {
				line-height: 40px;
				font-weight: 700;
				color: $red;
				padding: 0px 15px;
				// background: $red
				border: none;
				border-bottom: 2px solid $red;
				text-transform: uppercase;
				@include media-breakpoint-down(sm) {
					@include fs(16rem); } }
			tr {
				&:nth-child(even) {
					td {
						background: darken($white,3%); } } }
			td {
				border-bottom: 1px solid darken($white,20%);
				padding: 5px 15px;
				line-height: 30px;
				vertical-align: middle;
				&:first-child {}
				a {
					display: block;
					color: $main;
					&:hover {
						color: $red; }
					.fa {
						margin-right: 10px;
						color: $red; } }
				.new {
					display: block;
					background: $red;
					color: $white;
					text-align: center;
					padding: 0px 5px;
					@include bor-rad(3px); }
				.hot {
					display: block;
					background: $amber;
					color: $white;
					text-align: center;
					padding: 0px 5px;
					@include bor-rad(3px); } } } }
	.btn-apply, .frame-link {
		@include mainbutton;
		margin-right: 10px; }
	.download-link {
		@include mainbutton;
		background: $grey;
		&:hover {
			background: lighten($grey,5%); } }
	h3 {
		color: $red;
		font-weight: 600; } }

.apply-frm {
	padding-top: 20px;
	.col-left, .col-right {
		width: 50%;
		float: left;
		@include media-breakpoint-down(sm) {
			width: 100%;
			float: none;
			padding: 0; } }
	.col-left {
		padding-right: 15px;
		@include media-breakpoint-down(sm) {
			padding: 0; } }
	.col-right {
		padding-left: 15px;
		@include media-breakpoint-down(sm) {
			padding: 0; } }
	h4 {
		color: $black;
		font-weight: 500;
		text-transform: uppercase;
		margin-bottom: 15px;
		@include fs(15rem);
		// border-left: 3px solid $brown
		line-height: 1.5;
 }		// padding: 0 10px
	.form-group {
		margin-bottom: 10px;
		clear: both;
		&:before, &:after {
			content: '';
			display: table;
			width: 100%;
			clear: both; } }
	label {
		margin-bottom: 5px;
		.required {
			color: $red; } }
	.form-control {
		@include bor-rad(0);
		@include fs(14rem);
		padding: 10px 15px;
		border: 1px solid darken($white,10%);
		&:hover {
			border-color: lighten($blue,50%); }
		&:active, &:focus {
			border-color: $blue; } }
	textarea {
		min-height: 200px; }
	.btn {
		@include mainbutton; } }

.job-detail {
	.news-other {
		padding-top: 30px; }
	.title-left {
		margin-bottom: 20px; }
	h5 {
		padding: 10px 0;
		border-bottom: 1px dashed darken($white,10%);
		a {
			color: $red;
			@include fs(16rem);
			&:hover {
				text-decoration: underline; } } } }
