.login-page, .signup-page {
	padding: 60px 0; }

.login-panel, .signup-panel {
	background: $white;
	padding: 0 15px;
	margin-bottom: 40px;
	box-shadow: 0px 3px 30px color-rgba($black,0.1);
	@include bor-rad(5px);
	border: 1px solid darken($white,10%); }
.signup-form, .login-form {
	width: 100%;
	.headtitle {
		margin-bottom: 10px;
		color: $black;
		@include fs(15rem);
		line-height: 40px;
		font-weight: 700;
		border-bottom: 1px solid darken($white,10%);
		// margin-bottom: 0
		&:after {
			margin-top: 0; } }
	.panel-heading {
		margin-bottom: 5px;
		color: darken($white,60%); }
	.form-group {
		margin-bottom: 10px;
		label {
			@include fs(14rem);
			margin-bottom: 5px;
			line-height: 36px; }
		input, select {
			width: 100%;
			@include bor-rad(3px);
			font-size: 0.9rem;
			border: 1px solid darken($white,15%);
			padding: 0px 15px;
			line-height: 36px;
			&:hover, &:active, &:focus {
				border-color: darken($white,30%); } }
		select {
			padding: 10px 15px; }
		.fa.fa-exclamation {
			top: 42px;
			right: 10px; } }
	.btnwrap {
		text-align: center;
		padding: 10px 0; }
	input[type="submit"] {
		display: block;
		width: 100%;
		line-height: 36px;
		padding: 0px 20px;
		@include bor-rad(0px);
		background: $brown;
		color: $white;
		font-weight: 300 !important;
		@include fs(14rem);
		border: none;
		text-transform: uppercase;
		&:hover {
			background: darken($brown,10%); } }
	.social-login {
		background: darken($white,5%);
		padding: 10px;
		margin: 0px -15px;
		.des {
			font-weight: 700;
			color: $main; }
