.login-form {
	.form-group {
		width: 100%;
		@include pos(rel,null);
		label {
			width: 40px;
			line-height: 40px;
			text-align: center;
			@include fs(18rem);
			color: $brown;
			@include pos(abs, 0 null null 0);
			.fa {
				color: $brown !important;
				&:before {
					line-height: 1; } } }
		input {
			padding-left: 40px; }
		a {
			color: darken($white,60%);
			margin-right: 10px;
			&:hover {
				color: $blue; } }
		&.forget {
			@extend .check-group;
			label {
				width: auto;
				line-height: 1.5;
				@include fs(13rem);
				@include pos(static,null);
				color: darken($white,60%);
				&:hover {
					color: $blue; } } } }

	input[type="submit"] {
		padding: 0px 20px;
		border: none; }
	.btn-login {
		text-align: center;
		background: none;
		&:before {
			display: none; } } }

.signup-panel {
	background: $white;
	margin-bottom: 40px;
	.col-xs-12 {
		padding: 0 !important; } }
.signup-form {
	padding: 0px 15px 15px;
	.form-group {
		@include flexbox;
		label {
			margin-bottom: 0;
			@include fs(14rem);
			width: 140px;
			line-height: 30px; }
		.settingcontrol {
			width: calc(100% - 130px);
			line-height: 30px;
			input {
				line-height: 30px;
				padding: 0px 10px; }
			select {
				padding: 6px 10px; } } }
	.gender-select {
		ul {
			margin-top: 3px;
			li {
				display: inline-block;
				label {
					line-height: 1.5; } } } }
	.checkbox-button {
		label {
			width: auto;
			line-height: 1.5; } }
	.btnwrap {
		margin-bottom: 15px; } }
.signupnote {
	height: 100%;
	background: $brown; }
