
.select-group, .radio {
	margin-bottom: 10px;
	input[type="radio"] {
		display: none;
		+ label {
			padding: 5px 0 5px 20px;
			@include pos(rel,null);
			color: darken($white,60%);
			@include transition(all,0.3s);
			@include fs(14rem);
			@include media-breakpoint-down(xs) {
				padding: 0px 0 0 20px; }
			&:hover {
				color: $brown; }
			&:before {
				content: '';
				display: block;
				width: 14px;
				height: 14px;
				@include bor-rad(50px);
				@include pos(abs, 7px null null 0);
				border: 1px solid darken($white,40%);
				@include media-breakpoint-down(xs) {
					top: 3px; } }
			&:after {
				content: '';
				display: block;
				width: 14px;
				height: 14px;
				border: 4px solid $brown;
				@include pos(abs, 7px null null 0);
				@include bor-rad(50px);
				opacity: 0;
				@include transition(all,0.3s);
				@include media-breakpoint-down(xs) {
					top: 3px; } } }
		&:checked {
			+ label {
				color: $main;
				&:after {
					opacity: 1; } } } } }

.check-group, .checkbox {
	margin-bottom: 10px;
	input {
		display: none;
		+ label {
			color: $main;
			padding-left: 25px;
			@include pos(rel,null);
			&:hover {
				color: $brown; }
			&:before {
				content: '';
				display: block;
				width: 16px;
				height: 16px;
				// +bor-rad(3px)
				border: 1px solid darken($white,20%);
				@include pos(abs, 1px null null 0); }
			&:after {
				content: '\f00c';
				font-family: 'fontawesome';
				display: block;
				width: 16px;
				height: 16px;
				line-height: 14px;
				text-align: center;
				// +bor-rad(3px)
				color: $brown;
				border: 1px solid darken($white,20%);
				@include pos(abs, 1px null null 0);
				z-index: 1;
				@include transition(all,0.5s);
				opacity: 0; } }
		&:checked {
			+ label {
				&:after {
					opacity: 1; } } } } }
